.list-stripped {
  list-style: none;
  padding-left: 0; }

.list-inline {
  display: inline-block; }

.list-icon {
  margin-bottom: 7px; }

.nav {
  width: 100%; }
  .nav > a {
    background: #fff;
    border: 1px solid #39b54a;
    color: #484848;
    display: inline-block;
    margin: 0 3px 7px;
    text-decoration: none;
    padding: 7px; }
    .nav > a sub {
      display: inline-block;
      margin-bottom: 4px;
      margin-left: 5px;
      vertical-align: middle; }
    .nav > a:hover, .nav > a.selected {
      background: #39b54a;
      color: #fff; }
  .nav div {
    padding: 7px 10px 5px; }

.list-icon > span {
  display: inline-block;
  margin-right: 10px; }



